body {
  background: #000000;
  color: #ffffff;
}

section {
  background-color: rgba(0, 0, 0, 0.452);
  border-bottom: 2px solid #2e2e2e;
  border-radius: 2px;
  max-width: 100vw;
  padding: 5rem;
  margin: 0 auto;
}

.apresentation {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  grid-column-gap: 1rem;
  grid-row-gap: 1rem;
  justify-content: center;
  align-items: center;
  &__photo {
    grid-row: 1 / 3;
  }
  &__photo img {
    width: 30rem;
    border-radius: 50%;
    border: #ffffff solid 2px;
  }
  &__name {
    grid-column: 2 / 4;
    grid-row: 1;
    text-align: center;
    & h1 {
      font-size: 7rem;
    }
  }
  &__stacks {
    grid-column: 2 / 4;
    grid-row: 2;
    text-align: center;
    & img {
      display: inline;
    }
  }
}

.card {
  &__title h3 {
    font-size: 3rem;

  }
  &__text {
    font-size: 2rem;
  }
}


@media screen and (max-width: 800px) {
  .apresentation {
    display: flex;
    flex-direction: column;
    &__name {
      & h1 {
        font-size: 5rem;
      }
    }
    &__stacks {
      & img {
        width: 5rem;
      }
    }
  }
  html {
    font-size: 50%;
  }
}